@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap);
.Header{
    width: 100%;
    border: 0px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.searchBar{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background: white;
    border-radius: 10px;
}

.locationInput{
    border: 0px;
    outline: none;
    font-size: 1.7rem;
}

.searchButton{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    border-radius: 10px;
    padding: 5px;
    background: rgba(81,179,255,1);
    width: 40px;
    height: 40px;
    transition-duration: 0.2s;
    box-shadow: 1px 1px 2px 0px rgba(0,0,0,0.25);
}

.searchButton:hover{
    background: rgb(34, 104, 255);
}
*{
    box-sizing: border-box;
    font-family: 'Nunito Sans', sans-serif;
}

html{
    background: rgb(9,1,144);
    background: linear-gradient(3deg, rgb(255, 193, 132) 0%, rgba(81,179,255,1) 80%);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
}
body{
    margin: 0;
    padding: 0;
}

.App {
    height: 100%;
}

.grid{
    height: 100%;
    display: grid;
    margin: 20px;
    grid-gap: 20px;
    grid-template-columns: 0.5fr 1.5fr;
    grid-template-rows: 80px auto auto;
    justify-content: center;
    align-items: center;
}
.Header {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
}
.Current {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 4;
}
.DetailedDaily {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
}

.DetailedHourly {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
}
.Tab {
    display: inline-block;
    height: 100%;
    width: 100%;
    background: white;
    background: linear-gradient(30deg, rgb(255,255,255) 30%, rgb(230, 230, 230) 100%);
    border-radius: 10px;
    padding: 20px;
}
.Current{
    background: rgb(39, 103, 219);
    background: linear-gradient(45deg, #0E96DC 80%, rgb(238, 218, 59) 100%);
}

h1{
    margin: 0;
}

h2{
    margin: 0;
}
h3{
    display:inline-block;
}

.currentElement{
    margin-bottom: 10px;
    color: white;
}

.currentTemp{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.8em;
}

.minMax{
    margin-bottom: 20px;
}
.h3{
    display: inline-block;
}

.dailyTab{
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
}

.hourlyContainer{
    display: flex;
    flex-direction: row;
    overflow-x: auto;
}

.hourlyItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 10px;
}
h3{
    margin: 0;
}

.hourlyTab{
    display: flex;
    flex-direction: column;
    overflow-x: auto;
}

.DetailedHourly{
    display: flex;
    overflow-x: auto;
}
.detailItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 10px;
}
.hourlyDetails{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: flex-start;
    text-align: center;
    background: #0E96DC;
    height: 100%;
    padding: 5px;
    border-radius: 5px;
    color: white;
}

.hourlySpan{
    display: block;
    margin-bottom: 5px;
}

@media screen and (max-width: 700px){
    .App{
        height: 100%;
        padding: 20px;
    }
    .grid{
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    .Header {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
    }
    .Current {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
    }
    .DetailedDaily {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 3;
        grid-row-end: 4;
    }

    .DetailedHourly {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 4;
        grid-row-end: 5;
    }
}
